/* Base Application Styles */
@font-face{
    font-family: "icons-turn-arrow";
    src: url('./icons/turn-arrow.eot') format('enbedded'), url('./icons/turn-arrow.woff') format('woff'), url('./icons/turn-arrow.ttf') format('truetype'), url('./icons/turn-arrow.svg') format('svg');
}

html,body {width: 100%;height: 100%;background-color: #222;}
.content{width:100%;height: 100%;}

.stage{position: relative;  height: 680px;  width: 100%;}
.img-sec{position: relative;height: 100%;width:100%;overflow: hidden;background-color: #ddd;-webkit-perspective:1800px;perspective:1800px;}
.img-sec .img-figure{position: absolute;width: 320px;height: 360px;margin:0;padding: 40px;-webkit-box-sizing:border-box;box-sizing:border-box;background-color: #fff;cursor: pointer;-webkit-transform-origin: 0 50% 0;-ms-transform-origin: 0 50% 0;transform-origin: 0 50% 0;/* 被转换元素的位置 3D的分别对应: x轴，y轴，z轴，默认是：50% 50% 0 */-webkit-transform-style: preserve-3d;transform-style: preserve-3d;/* 子元素将保留其3D位置 */-webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;-o-transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;/* transform、left、top分别应用过渡效果，.6秒完成，慢速开始和结束 */}
.img-sec .img-figure img{width:240px;height:240px;}
.img-sec .img-figure.is-inverse{-webkit-transform: translate(320px) rotateY(180deg);transform: translate(320px) rotateY(180deg);}

figcaption{text-align: center;}
figcaption .img-title{margin: 20px 0 0 0;color: #a7a0a2;font-size: 16px;}
figcaption .img-back{position: absolute;/* 相对于img-sec进行定位 */top: 0;left: 0;width: 100%;height: 100%;-webkit-box-sizing: border-box;box-sizing: border-box;padding: 50px 40px;overflow: auto;color: #666;background: #fff;-webkit-transform: rotateY(180deg) translateZ(1px);transform: rotateY(180deg) translateZ(1px);}

/*控制点*/
.controller-nav{position: absolute;left: 0;bottom: 30px;z-index: 2;width: 100%;text-align: center;}
.controller-unit{display: inline-block;width:30px;height: 30px;margin:0 5px;text-align: center;cursor: pointer;background-color: #aaa;border-radius: 50%;-webkit-transform:scale(.5);-ms-transform:scale(.5);transform:scale(.5);vertical-align: middle;transform:scale(.5);-webkit-transition:background-color .3s,-webkit-transform .6s ease-in-out;transition:background-color .3s,-webkit-transform .6s ease-in-out;-o-transition:transform .6s ease-in-out,background-color .3s;transition:transform .6s ease-in-out,background-color .3s;transition:transform .6s ease-in-out,background-color .3s,-webkit-transform .6s ease-in-out;}
.controller-unit.is-center{-webkit-transform:scale(1);-ms-transform:scale(1);transform:scale(1);background-color: #888;}
.controller-unit.is-center:after{font-family: "icons-turn-arrow";content: '\e600';color:#fff;line-height: 30px;-webkit-font-smoothing:anitialiased;-moz-osx-font-smoothing:grayscale;}
.controller-unit.is-center.is-inverse{-webkit-transform:rotateY(180deg);transform:rotateY(180deg);background-color: #555;}












